import React, { useEffect, useState } from "react";
import style from "./HomeContent.scss";
import classnames from "classnames/bind";
import SlickList from "../SlickList";
import BookList from "../BookList";
import VerticalScroll from "../VerticalScroll";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faAngleDown, faAngleUp } from "@fortawesome/free-solid-svg-icons";
import { v4 } from "uuid";

const cx = classnames.bind(style);
const HomeContent = ({
  booklist,
  handleClick,
  handleMore,
  sliderBooks,
  recentBooks,
  popularBooks,
  type,
  done,
}) => {
  const [isMobile, setIsMobile] = useState(false);
  const [hide, setHide] = useState(true);
  const [category, setCategory] = useState([
    "",
    "",
    "IT",
    "",
    "",
    "",
    "",
    "",
    "",
    "",
  ]);
  const checkSize = () => {
    const innerWidth = window.innerWidth;
    if (innerWidth >= 320 && innerWidth <= 425) {
      setIsMobile(true);
    } else {
      setIsMobile(false);
    }
  };
  const toggleHide = () => {
    setHide((cur) => !cur);
  };

  useEffect(() => {
    checkSize();
    window.addEventListener("resize", checkSize);
    return () => {
      window.removeEventListener("resize", checkSize);
    };
  });
  return (
    <section className={cx("home-content")}>
      <SlickList books={sliderBooks} />
      <VerticalScroll
        title={"  "}
        books={recentBooks.slice(0, 15)}
      />
      <VerticalScroll title={"  "} books={popularBooks} />
      <section className={cx("books-wrapper")}>
        <section className={cx("book-category-section")}>
          <div className={cx("book-category-div")}>
            <ul className={cx("book-category-ul")}>
              {hide
                ? category.slice(0, 7).map((item) => (
                    <li
                      key={v4()}
                      className={cx(
                        `book-category-li`,
                        `${type === item ? `selected` : ""}`
                      )}
                      onClick={handleClick}
                      data-value={item}
                    >
                      {item}
                    </li>
                  ))
                : category.map((item) => (
                    <li
                      key={v4()}
                      className={cx(
                        `book-category-li`,
                        `${type === item ? `selected` : ""}`
                      )}
                      onClick={handleClick}
                      data-value={item}
                    >
                      {item}
                    </li>
                  ))}
            </ul>
          </div>
          <div className={cx("more-container")} onClick={toggleHide}>
            {hide ? " " : ""}
            <FontAwesomeIcon
              className={cx("more")}
              icon={hide ? faAngleDown : faAngleUp}
            />
          </div>
        </section>
        <BookList
          booklist={booklist}
          handleMore={handleMore}
          from={"home"}
          type={type}
          done={done}
        />
      </section>
    </section>
  );
};

export default HomeContent;
